body {
  font-family: "Montserrat", "Helvetica", Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #66615b;
  height: 100%;
  padding-top: 50px;
  background: #fff; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

ol, ul {
  list-style: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

h1, h2, h3, h4 {
  font-weight: 400; }

h1 {
  font-size: 4.5em;
  position: relative;
  margin-bottom: 7px; }

h2 {
  font-size: 3em;
  position: relative; }

h3 {
  font-size: 1.825em; }

h4 {
  font-size: 1.5em;
  font-weight: 600;
  line-height: 1.2em; }

h5 {
  font-size: 1.25em;
  font-weight: 400;
  line-height: 1.4em; }

h6, .h6 {
  font-size: 0.9em;
  font-weight: 600; }

h1 small, h2 small, h3 small, h1 .small, h2 .small, h3 .small {
  font-size: 60%; }

p {
  font-size: 16px;
  margin-top: 10px; }

a {
  color: #eb5e28;
  text-decoration: none;
  transition: all 0.3s linear;
  -moz-transition: all 0.3s linear;
  -webkit-transition: all 0.3s linear;
  -o-transition: all 0.3s linear; }

a:hover {
  color: #868991;
  text-decoration: none; }

/* Bootstrap Grid
-------------------------------------------------- */
.container-fluid {
  margin-left: 0;
  margin-right: 0;
  padding-left: 0px;
  padding-right: 0px; }

.row.no-gutters,
.content.no-gutters,
.wrapper-content.no-gutters {
  margin-right: 0;
  margin-left: 0; }
  .row.no-gutters > [class^="col-"],
  .row.no-gutters > [class*=" col-"],
  .content.no-gutters > [class^="col-"],
  .content.no-gutters > [class*=" col-"],
  .wrapper-content.no-gutters > [class^="col-"],
  .wrapper-content.no-gutters > [class*=" col-"] {
    padding-right: 0;
    padding-left: 0; }

/* Seccin Cursos destacados */
#destacados {
  position: relative;
  width: 100%; }

.curso .curso-info {
  position: relative;
  bottom: 85px;
  padding-left: 15px;
  padding-right: 15px;
  color: white;
  width: 100%;
  height: auto; }

.curso .curso-info > a {
  color: white; }

.curso .curso-info > a > p {
  margin: 0;
  padding: 0; }

.curso img {
  min-width: 100%;
  min-height: 100%;
  width: 100%;
  height: 338px;
  margin: 0;
  border: 0; }

.curso-titulo {
  display: inline-block;
  width: 100%;
  font-size: 12px;
  font-weight: 600;
  color: ghostwhite;
  text-transform: uppercase; }

.curso-desc {
  font-size: 12px;
  font-weight: 500;
  margin: 5px 0 20px 0; }

.overlay-wrp-dark {
  position: absolute;
  top: 74%;
  left: inherit;
  display: block;
  height: 27%;
  width: 100%; }

.overlay-wrp-dark .overlay-dark {
  opacity: 0.5;
  filter: alpha(opacity=50);
  height: 100%;
  width: 100%; }

.overlay-dark {
  background: black; }

/* Seccin Argumento de venta */
#culture {
  width: 100%;
  padding: 0 0;
  color: white; }

.argv {
  height: 100%;
  width: 100%;
  padding: 50px 0; }

.argv:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(230, 92, 0, 0.5); }

.argv-text {
  width: 50%;
  margin-left: 20px;
  padding: 30px 0;
  font-size: 20px;
  line-height: 1.5;
  text-align: left;
  position: relative;
  color: white; }

/* Seccin Programas (Secciones AAVI) */
.seccion-caption {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  color: white;
  text-align: center;
  -webkit-transform: translate3d(-50%, -50%, 0);
  -khtml-transform: translate3d(-50%, -50%, 0);
  -moz-transform: translate3d(-50%, -50%, 0);
  -ms-transform: translate3d(-50%, -50%, 0);
  -o-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0); }

.seccion-caption > h5 {
  color: #f1f1f1;
  font-weight: 900;
  text-transform: uppercase;
  border: 4px solid #f1f1f1;
  width: 85%;
  float: none;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 0; }

/* Commons */
.box-item {
  width: 100%;
  height: 338px;
  padding: 0;
  border: 0;
  overflow: hidden; }

img.scaleimg {
  width: 100%;
  height: 100%; }

/*Front Page*/
.path-frontpage .feed-icon {
  display: none; }

.path-frontpage .content_layout {
  max-width: 750px;
  margin: 0 auto;
  padding: 20px 0;
  overflow: hidden; }

/*Breadcrumb*/
.breadcrumb {
  font-size: .85em;
  padding: 3px 10px;
  margin: 0;
  background: #fff;
  display: inline-block; }
  .breadcrumb ul {
    padding: 0;
    margin: 0; }
    .breadcrumb ul li {
